
import XNumber from 'vux/src/components/x-number/index.vue'

export default {
name: "Second",
components: {
XNumber
},
data() {
return {
msg: "第二个页面",
data: [
{
text: "苹果🍎",
number: 1,
price: 88.88,
selected: false
},
{
text: "南瓜🎃",
number: 1,
price: 66.66,
selected: false
},
{
text: "葡萄🍇",
number: 1,
price: 55.55,
selected: false
},
{
text: "草莓🍓",
number: 1,
price: 11.11,
selected: false
},
{
text: "西瓜🍉",
number: 1,
price: 22.22,
selected: false
}
]
};
},
computed: {
title() {
return this.$store.state.title;
},

totalPrice() {
var total = 0;
for (var i = 0; i < this.data.length; i++) {
if (this.data[i].selected) {
total += this.data[i].number * this.data[i].price;
}
}
return total.toFixed(2);
}
},
methods: {
goBack() {
this.$router.push("/");
},

selList(index) {
this.data[index].selected = !this.data[index].selected;
},

choose(type) {
for (var i = 0; i < this.data.length; i++) {
type == "全选"
? (this.data[i].selected = true)
: (this.data[i].selected = !this.data[i].selected);
}
}
}
};



